//Links*/

@import 'colors.scss';

a:link {
    text-decoration: none;
    color: $color-accent;
}
        
a:hover {
    text-decoration: underline;
    color: darken($color-accent, 10%);
}
        
a:active {
    outline: 0;
    outline: none;
}
        
a:visited {
    outline: 0;
    outline: none;
    color: $color-primary;
}
        
a:focus {
    outline: 0;
    outline: none;
    text-decoration: underline;	
}
